<template>
  <BlogHeader ref="headerRef" />
  <div id="user-center">
    <h3>Êõ¥Êñ∞ËµÑÊñô‰ø°ÊÅØ</h3>
    <form>
      <div class="form-elem">
        <span>Áî®Êà∑ÂêçÔºö</span>
        <input v-model="username" type="text" placeholder="ËæìÂÖ•Áî®Êà∑Âêç" />
      </div>

      <div class="form-elem">
        <span>Êñ∞ÂØÜÁ†ÅÔºö</span>
        <input v-model="password" type="password" placeholder="ËæìÂÖ•ÂØÜÁ†Å" />
      </div>

      <div class="form-elem">
        <button v-on:click.prevent="changeInfo">Êõ¥Êñ∞</button>
      </div>

      <div class="form-elem">
        <button
          v-on:click.prevent="showingDeleteAlert = true"
          class="delete-btn"
        >
          Âà†Èô§Áî®Êà∑
        </button>
        <div :class="{ shake: showingDeleteAlert }">
          <button
            v-if="showingDeleteAlert"
            class="confirm-btn"
            @click.prevent="confirmDelete"
          >
            Á°ÆÂÆöÔºü
          </button>
        </div>
      </div>

      <div class="form-back">
        <router-link :to="{ name: 'BlogHome' }">ËøîÂõûÂçöÂÆ¢È¶ñÈ°µ</router-link>
      </div>
    </form>
  </div>
  <BlogFooter />
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import axios from 'axios';
import BlogHeader from '@/components/BlogHeader.vue';
import BlogFooter from '@/components/BlogFooter.vue';

import authorization from '@/utils/authorization';

const storage = localStorage;

const username = ref('');
const password = ref('');
const token = ref('');
const showingDeleteAlert = ref(false);
const headerRef = ref(null);

const router = useRouter();

onMounted(() => {
  // ‰ªéÊú¨Âú∞Â≠òÂÇ®‰∏≠Ëé∑ÂèñÁî®Êà∑ÂêçÔºåÂπ∂ËµãÂÄºÁªôÁªÑ‰ª∂ÁöÑ username Êï∞ÊçÆ
  username.value = storage.getItem('username.myblog');
});

const confirmDelete = () => {
  authorization().then(function (response) {
    if (response[0]) {
      // Ëé∑Âèñ‰ª§Áâå
      token.value = storage.getItem('access.myblog');
      axios
        .delete('/api/user/' + username.value + '/', {
          headers: { Authorization: 'Bearer ' + token.value },
        })
        .then(function () {
          storage.clear();
          router.push({ name: 'BlogHome' });
        });
    }
  });
};
const changeInfo = () => {
  // È™åËØÅÁôªÂΩïÁä∂ÊÄÅ
  authorization().then(function (response) {
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    // Ëã•ÁôªÂΩïÂ∑≤ËøáÊúüÂàô‰∏çÊâßË°åÂêéÁª≠Êìç‰Ωú
    if (!response[0]) {
      alert('ÁôªÂΩïÂ∑≤ËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï„ÄÇ');
      return;
    }
    // Â¶ÇÊûúÁôªÂΩïÁä∂ÊÄÅÊúâÊïàÔºåÂàôÂú®ÊéßÂà∂Âè∞ËæìÂá∫Êó•ÂøóÔºåË°®Á§∫ÂºÄÂßãÊõ¥Êîπ‰ø°ÊÅØ
    console.log('change info start.');
    // ÂØÜÁ†Å‰∏çËÉΩÂ∞è‰∫é6‰Ωç
    if (password.value.length > 0 && password.value.length < 6) {
      alert('„Éë„Çπ„ÉØ„Éº„Éâ„ÅØÁü≠„ÅÑ„Åß„Åô„ÄÇ');
      return;
    }
    // ÊóßÁöÑ username Áî®‰∫éÊé•Âè£ÂèëÈÄÅËØ∑Ê±Ç
    const oldName = storage.getItem('username.myblog');
    // Ëé∑ÂèñÂ∑≤Â°´ÂÜôÁöÑË°®ÂçïÊï∞ÊçÆ
    let data = {};
    if (username.value !== '') {
      data.username = username.value;
    }
    if (password.value !== '') {
      data.password = password.value;
    }
    // Ëé∑Âèñ‰ª§Áâå
    token.value = storage.getItem('access.myblog');
    // ÂèëÈÄÅÊõ¥Êñ∞Êï∞ÊçÆÂà∞Êé•Âè£
    axios
      .patch('/api/user/' + oldName + '/', data, {
        headers: { Authorization: 'Bearer ' + token.value },
      })
      .then(function (response) {
        // ‰ªéÂìçÂ∫î‰∏≠Ëé∑ÂèñÊõ¥Êñ∞ÂêéÁöÑÁî®Êà∑Âêç
        const name = response.data.username;
        // Â∞ÜÊõ¥Êñ∞ÂêéÁöÑÁî®Êà∑ÂêçÂ≠òÂÇ®Âà∞Êú¨Âú∞Â≠òÂÇ®‰∏≠
        storage.setItem('username.myblog', name);
        // ‰ΩøÁî®Ë∑ØÁî±Âô®Ë∑≥ËΩ¨Âà∞Áî®Êà∑‰∏≠ÂøÉÈ°µÈù¢ÔºåÂπ∂‰º†ÈÄíÊõ¥Êñ∞ÂêéÁöÑÁî®Êà∑Âêç‰Ωú‰∏∫ÂèÇÊï∞
        router.push({
          name: 'UserCenter',
          params: { username: name },
        });
        headerRef.value.refresh;
      });
  });
};
</script>

