import { ref } from 'vue';
import { useRouter } from 'vue-router';
import axios from 'axios';

const signinName = ref('');
const signinPwd = ref('');
const signinError = ref('');

export default function userSignin() {
  const router = useRouter();
  const signin = () => {
    if (!signinName.value.trim() || !signinPwd.value.trim()) {
      alert('ËØ∑ËæìÂÖ•Áî®Êà∑ÂêçÊàñÂØÜÁ†Å');
      return;
    }

    axios
      // Âêë /api/token/ Ë∑ØÂæÑÂèëÈÄÅ‰∫Ü‰∏Ä‰∏™ÂåÖÂê´Áî®Êà∑ÂêçÂíåÂØÜÁ†ÅÁöÑËØ∑Ê±Ç‰Ωì„ÄÇ
      .post('/api/token/', {
        username: signinName.value,
        password: signinPwd.value,
      })
      .then(function (response) {
        const storage = localStorage;
        // Date.parse(...) ËøîÂõû1970Âπ¥1Êúà1Êó•UTC‰ª•Êù•ÁöÑÊØ´ÁßíÊï∞
        // Token Ë¢´ËÆæÁΩÆÊàê100ÂàÜÈíüÔºåÂõ†Ê≠§ËøôÈáåÂä†‰∏ä 6000000 ÊØ´Áßí
        const expiredTime = Date.parse(response.headers.date) + 6000000;
        // ËÆæÁΩÆ localStorageÔºå Ëé∑ÂèñÊï∞ÊçÆ
        // Â∞Ü‰ªéÊúçÂä°Âô®ÂìçÂ∫î‰∏≠Ëé∑ÂèñÁöÑËÆøÈóÆ‰ª§ÁâåÂ≠òÂÇ®Âú®Êú¨Âú∞Â≠òÂÇ®‰∏≠
        storage.setItem('access.myblog', response.data.access);
        // Â∞Ü‰ªéÊúçÂä°Âô®ÂìçÂ∫î‰∏≠Ëé∑ÂèñÁöÑÂà∑Êñ∞‰ª§ÁâåÂ≠òÂÇ®Âú®Êú¨Âú∞Â≠òÂÇ®‰∏≠
        storage.setItem('refresh.myblog', response.data.refresh);
        // Â∞ÜËÆ°ÁÆóÂá∫ÁöÑËøáÊúüÊó∂Èó¥Â≠òÂÇ®Âú®Êú¨Âú∞Â≠òÂÇ®‰∏≠
        storage.setItem('expiredTime.myblog', expiredTime);
        // Â∞ÜÁî®Êà∑ÁôªÂΩïÁöÑÁî®Êà∑ÂêçÂ≠òÂÇ®Âú®Êú¨Âú∞Â≠òÂÇ®‰∏≠
        storage.setItem('username.myblog', signinName.value);

        // ÊòØÂê¶‰∏∫ÁÆ°ÁêÜÂëò
        axios
          .get('/api/user/' + signinName.value + '/')
          .then(function (response) {
            storage.setItem('isSuperuser.myblog', response.data.is_superuser);
            // Ë∑ØÁî±Ë∑≥ËΩ¨
            router.push({ name: 'BlogHome' });
          });
      })
      .catch(function (error) {
        signinError.value = 'ÁôªÂΩïÂ§±Ë¥•ÔºöÁî®Êà∑ÂêçÊàñÂØÜÁ†ÅÈîôËØØ' + error.message;
      });
  };
  return { signin, signinName, signinPwd, signinError };
}
