import axios from 'axios';

import { onMounted, watch } from 'vue';

export default function getArticleData(info, route) {
  const getData = async () => {
    // ÂáΩÊï∞Ëµ∑ÂßãÂ§ÑÂØπÂΩìÂâç query ÂèÇÊï∞ËøõË°åÁ≠âÂÄºÂà§Êñ≠
    // Ëã•‰∏∫ÈáçÂ§çËØ∑Ê±ÇÂàôÁ´ãÂç≥‰∏≠Êñ≠ÂáΩÊï∞
    const queryPage =
      route.query.page !== undefined ? parseInt(route.query.page) : 1;
    if (
      kwargs.value.page === queryPage &&
      kwargs.value.searchText === route.query.search
    ) {
      return;
    }

    let url = '/api/article';
    let params = new URLSearchParams();

    params.appendIfExists('page', route.query.page);
    params.appendIfExists('search', route.query.search);

    const paramsString = params.toString();
    if (paramsString.charAt(0) !== '') {
      url += '/?' + paramsString;
    }

    const response = await axios.get(url);
    info.value = response.data;

    // ÂØπÂΩìÂâçÈ°µÁöÑÁä∂ÊÄÅËµãÂÄº
    // Áî®‰∫é‰∏ãÊ¨°ËØ∑Ê±ÇÊó∂ËøõË°åÈáçÂ§çÊÄßÂà§Êñ≠
    kwargs.value.page = queryPage;
    kwargs.value.searchText = route.query.search;
  };

  onMounted(getData);
  watch(route, getData);
}
