import { ref } from 'vue';

import axios from 'axios';

const signupName = ref('');
const signupPwd = ref('');
const confirmPwd = ref('');
const signupResponse = ref(null);
const signupError = ref('');
const passwordsNotMatch = ref(false);

export default function userSignup() {
  const signup = () => {
    if (!signupName.value.trim() || !signupPwd.value.trim()) {
      alert('ËØ∑ËæìÂÖ•Áî®Êà∑ÂêçÊàñÂØÜÁ†Å');
      return;
    }

    if (signupPwd.value !== confirmPwd.value) {
      passwordsNotMatch.value = true;
      return; // ÂØÜÁ†Å‰∏çÂåπÈÖçÊó∂ÂÅúÊ≠¢Ê≥®ÂÜåÊµÅÁ®ã
    }

    axios
      .post('/api/user/', {
        username: signupName.value,
        password: signupPwd.value,
      })
      .then(function (response) {
        signupResponse.value = response.data;
        alert('Áî®Êà∑Ê≥®ÂÜåÊàêÂäüÔºåÂø´ÂéªÁôªÂΩïÂêßÔºÅ');
      })
      .catch(function (error) {
        // Âú®ËøôÈáåÂ§ÑÁêÜËØ∑Ê±ÇÂ§±Ë¥•ÁöÑÊÉÖÂÜµÔºåÊòæÁ§∫ÈîôËØØÊèêÁ§∫ÁªôÁî®Êà∑
        signupError.value = 'Ê≥®ÂÜåÂ§±Ë¥•Ôºö' + error.message;
      });
  };
  return {
    signup,
    signupName,
    signupPwd,
    confirmPwd,
    signupResponse,
    signupError,
    passwordsNotMatch,
  };
}
