import axios from 'axios';

// async Ë°®Á§∫ÂáΩÊï∞ÈáåÂê´ÊúâÂºÇÊ≠•Êìç‰Ωú
async function authorization() {
  const storage = localStorage;

  let hasLogin = false;
  let username = storage.getItem('username.myblog');

  // ËøáÊúüÊó∂Èó¥
  const expiredTime = Number(storage.getItem('expiredTime.myblog'));
  // ÂΩìÂâçÊó∂Èó¥
  const current = new Date().getTime();
  // Âà∑Êñ∞‰ª§Áâå
  const refreshToken = storage.getItem('refresh.myblog');

  // ÂàùÂßã TOken Êú™ËøáÊúü
  if (expiredTime > current) {
    hasLogin = true;
    console.log('authorization access');
  }
  // ÂàùÂßã Token ËøáÊúü
  // Â¶ÇÊûúÊúâÂà∑Êñ∞‰ª§ÁâåÂàôÁî≥ËØ∑Êñ∞ÁöÑtoken
  else if (refreshToken !== null) {
    try {
      // await Ë°®Á§∫Á¥ßË∑üÂú®ÂêéÈù¢ÁöÑË°®ËææÂºèÈúÄË¶ÅÁ≠âÂæÖÁªìÊûú
      // await ÂÖ≥ÈîÆÂ≠óÂè™ËÉΩÁî®Âú® async ÂáΩÊï∞‰∏≠ÔºåÂπ∂‰∏îÁî±‰∫éÂÆÉËøîÂõûÁöÑ Promise ÂØπË±°ËøêË°åÁöÑÁªìÊûúÂèØËÉΩÊòØ rejected ÔºåÊâÄ‰ª•ÊúÄÂ•ΩÊîæÂà∞ try...catch ËØ≠Âè•‰∏≠„ÄÇ
      // ‰ΩøÁî® axios.post ÊñπÊ≥ïÂèëÈÄÅÂºÇÊ≠• POST ËØ∑Ê±ÇÔºåÂêë /api/token/refresh/ ÂèëÈÄÅÂà∑Êñ∞‰ª§Áâå
      let response = await axios.post('/api/token/refresh/', {
        refresh: refreshToken,
      });

      // ËÆ°ÁÆóÊñ∞ÁöÑ Token ËøáÊúüÊó∂Èó¥ÔºåÂπ∂Â∞ÜÂÖ∂Â≠òÂÇ®Âú® localStorage ‰∏≠
      const nextExpiredTime = Date.parse(response.headers.date) + 60000;

      storage.setItem('access.myblog', response.data.access);
      storage.setItem('expiredTime.myblog', nextExpiredTime);
      storage.removeItem('refresh.myblog');

      hasLogin = true;

      console.log('authorization refresh');
    } catch (err) {
      storage.clear();
      hasLogin = false;

      console.log('authorization err');
    }
  }
  // Êó†‰ªª‰ΩïÊúâÊïà token
  else {
    storage.clear();
    hasLogin = false;
    console.log('authorization exp');
  }
  console.log('authorization done');

  return [hasLogin, username];
}

export default authorization;
