// ÂØºÂÖ•Vue RouterÂ∫ì‰∏≠ÁöÑcreateWebHistoryÂíåcreateRouterÂáΩÊï∞„ÄÇ
import { createWebHistory, createRouter } from 'vue-router';
// ÂØºÂÖ•ÁªÑ‰ª∂
import BlogHome from '@/views/BlogHome.vue';
import ArticleDetail from '@/views/ArticleDetail.vue';
import Login from '@/views/Login.vue';
import UserCenter from '@/views/UserCenter.vue';
import ArticleCreate from '@/views/ArticleCreate.vue';
import ArticleEdit from '@/views/ArticleEdit.vue';
import SignUp from '@/views/SignUp.vue';

// ÂÆö‰πâË∑ØÁî±ÈÖçÁΩÆÊï∞ÁªÑ
const routes = [
  {
    //Ë°®Á§∫Ëøô‰∏™Ë∑ØÁî±ËßÑÂàôÂåπÈÖçÁöÑË∑ØÂæÑÊòØÊ†πË∑ØÂæÑ
    path: '/',
    name: 'BlogHome',
    component: BlogHome,
  },
  {
    path: '/article/:id',
    name: 'ArticleDetail',
    component: ArticleDetail,
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
  },
  {
    path: '/signup',
    name: 'SignUp',
    component: SignUp,
  },
  {
    path: '/user/:username',
    name: 'UserCenter',
    component: UserCenter,
  },
  {
    path: '/article/create',
    name: 'ArticleCreate',
    component: ArticleCreate,
  },
  {
    path: '/article/edit/:id',
    name: 'ArticleEdit',
    component: ArticleEdit,
  },
];

const router = createRouter({
  // Áî® createWebHistory() ÂàõÂª∫ HTML5 Ê®°Âºè
  history: createWebHistory(),
  routes,
});

// ÂØºÂá∫ÂàõÂª∫ÁöÑË∑ØÁî±ÂÆû‰æãÔºå‰ª•‰æøÂú®Â∫îÁî®ÁöÑÂÖ∂‰ªñÈÉ®ÂàÜ‰∏≠‰ΩøÁî®„ÄÇ
export default router;
