<template>
  <BlogHeader />

  <div class="article-delete">
    <h3>Êõ¥Êñ∞ÊñáÁ´†</h3>
    <form>
      <div class="form-elem">
        <span>Ê†á È¢ò: </span>
        <input
          v-model="title"
          type="text"
          placeholder="„Çø„Ç§„Éà„É´„ÇíÂÖ•Âäõ„Åô„Çã..."
        />
      </div>

      <div class="form-elem">
        <span>ÂàÜ È°û: </span>
        <span v-for="category in categories" :key="category.id">
          <button
            class="category-btn"
            :style="categoryStyle(category)"
            @click.prevent="chooseCategory(category)"
          >
            {{ category.title }}
          </button>
        </span>
      </div>

      <div class="form-elem">
        <span>„É©„Éô„É´: </span>
        <input v-model="tags" type="text" placeholder="„É©„Éô„É´„ÇíÂÖ•Âäõ„Åô„Çã..." />
      </div>

      <div class="form-elem">
        <span>Ê≠£ Êñá: </span>
        <textarea
          v-model="body"
          placeholder="Ê≠£Êñá„ÇíÂÖ•Âäõ„Åô„Çã..."
          rows="24"
          cols="64"
        ></textarea>
      </div>

      <div class="form-elem">
        <button v-on:click.prevent="submit">Êèê‰∫§</button>
      </div>

      <div class="form-elem">
        <button
          @click.prevent="deleteArticle"
          style="
            background-image: linear-gradient(to top left, #e52a5a, #ff585f);
          "
        >
          Ê∂àÈô§
        </button>
      </div>
    </form>
  </div>

  <BlogFooter />
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import BlogHeader from '@/components/BlogHeader.vue';
import BlogFooter from '@/components/BlogFooter.vue';
import axios from 'axios';
import authorization from '@/utils/authorization';

const title = ref('');
const body = ref('');

// ÊâÄÊúâÂàÜÁ±ª
const categories = ref([]);
// ÈÄâÂÆöÁöÑÂàÜÁ±ª
const selectedCategory = ref(null);
// Ê†áÁ≠æ
const tags = ref('');

// Article id
const articleID = ref(null);

const route = useRoute();
const router = useRouter();

onMounted(() => {
  // È°µÈù¢ÂàùÂßãÂåñÊó∂Ëé∑ÂèñÊâÄÊúâÂàÜÁ±ª
  axios
    .get('/api/category/')
    .then(response => (categories.value = response.data));
});

onMounted(() => {
  // È°µÈù¢ÂàùÂßãÂåñÊó∂Ëé∑ÂèñÊâÄÊúâÂàÜÁ±ª
  axios
    .get('/api/category/')
    .then(response => (categories.value = response.data));

  // Â¶ÇÊûú‰∏çÂ∏åÊúõÈùûÁÆ°ÁêÜÂëòÁî®Êà∑‰πüËÉΩËé∑ÂèñÂéüÂßã Markdown Êï∞ÊçÆ
  // ÈÇ£‰πàÂøÖÈ°ªÂú®ÂêéÁ´ØËøõË°åÈâ¥ÊùÉ
  // Ê†πÊçÆÁî®Êà∑Ë∫´‰ªΩÈÄâÁî®‰∏çÂêåÁöÑÂ∫èÂàóÂåñÂô®

  axios.get('/api/article/' + route.params.id + '/').then(function (response) {
    const data = response.data;
    title.value = data.title;
    body.value = data.body;
    selectedCategory.value = data.category;
    tags.value = data.tags.join(',');

    articleID.value = data.id;
  });
});

// Ê†πÊçÆÂàÜÁ±ªÊòØÂê¶Ë¢´ÈÄâ‰∏≠ÔºåÊåâÈíÆÁöÑÈ¢úËâ≤ÂèëÁîüÂèòÂåñ
const categoryStyle = category => {
  if (
    selectedCategory.value !== null &&
    category.id === selectedCategory.value.id
  ) {
    return {
      backgroundColor: 'black',
    };
  }
  return {
    backgroundColor: 'lightgrey',
    color: 'black',
  };
};
// ÈÄâÂèñÂàÜÁ±ª
const chooseCategory = category => {
  // Â¶ÇÊûúÁÇπÂáªÂ∑≤ÈÄâÂèñÁöÑÂàÜÁ±ªÔºåÂàôÂ∞Ü selectedCategory ÁΩÆÁ©∫
  if (
    selectedCategory.value !== null &&
    selectedCategory.value.id === category.id
  ) {
    selectedCategory.value = null;
  } else {
    selectedCategory.value = category;
  }
};

const submit = () => {
  authorization().then(function (response) {
    if (response[0]) {
      let data = {
        title: title.value,
        body: body.value,
      };

      data.category_id = selectedCategory.value
        ? selectedCategory.value.id
        : null;

      data.tags = tags.value
        .split(/[,Ôºå]/)
        .map(x => x.trim())
        .filter(x => x.charAt(0) !== '');

      const token = localStorage.getItem('access.myblog');
      axios
        .put('/api/article/' + articleID.value + '/', data, {
          headers: { Authorization: 'Bearer ' + token },
        })
        .then(function (response) {
          router.push({
            name: 'ArticleDetail',
            params: { id: response.data.id },
          });
        });
    } else {
      alert('‰ª§ÁâåËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï„ÄÇ');
    }
  });
};

const deleteArticle = () => {
  const token = localStorage.getItem('access.myblog');
  authorization().then(function (response) {
    if (response[0]) {
      axios
        .delete('/api/article/' + articleID.value + '/', {
          headers: { Authorization: 'Bearer ' + token },
        })
        .then(() => router.push({ name: 'BlogHome' }));
    } else {
      alert('‰ª§ÁâåËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï„ÄÇ');
    }
  });
};
</script>

