<template>
  <br /><br />
  <hr />
  <h3>ÂèëË°®ËØÑËÆ∫</h3>
  <!-- ËØÑËÆ∫Â§öË°åÊñáÊú¨ËæìÂÖ•Êéß‰ª∂ -->
  <textarea
    v-model="message"
    :placeholder="placeholder"
    name="comment"
    id="comment-area"
    cols="60"
    rows="10"
  ></textarea>
  <div>
    <button @click="submit" class="submitBtn">ÂèëÂ∏É</button>
  </div>

  <br />
  <p>Â∑≤Êúâ {{ comments.length }} Êù°ËØÑËÆ∫</p>
  <hr />

  <!-- Ê∏≤ÊüìÊâÄÊúâËØÑËÆ∫ÂÜÖÂÆπ -->
  <div v-for="comment in comments" :key="comment.id">
    <div class="comments">
      <div>
        <span class="username">
          {{ comment.author.username }}
        </span>
        ‰∫é
        <span class="created">
          {{ formatted_time(comment.created) }}
        </span>
        <span v-if="comment.parent">
          ÂØπ
          <span class="parent">
            {{ comment.parent.author.username }}
          </span>
        </span>
        ËØ¥ÈÅìÔºö
      </div>
      <div class="content">
        {{ comment.content }}
      </div>
      <div>
        <button class="commentBtn" @click="replyTo(comment)">ÂõûÂ§ç</button>
      </div>
    </div>
    <hr />
  </div>
</template>

<script setup>
import axios from 'axios';
import authorization from '@/utils/authorization';
import formattedTime from '../composables/formattedTime';
import { ref, watchEffect } from 'vue';

const props = defineProps({
  article: Object,
});

// ÊâÄÊúâËØÑËÆ∫
const comments = ref([]);
// ËØÑËÆ∫Êéß‰ª∂ÁªëÂÆöÁöÑÊñáÊú¨ÂíåÂç†‰ΩçÁ¨¶
const message = ref('');
const placeholder = ref('Say something ...');
// ËØÑËÆ∫ÁöÑËØÑËÆ∫
const parentID = ref(null);

// ÁõëÂê¨ article ÂØπË±°Ôºå ‰ª•‰æøÂÆûÊó∂Êõ¥Êñ∞ËØÑËÆ∫
watchEffect(() => {
  comments.value = props.article !== null ? props.article.comments : [];
});

const submit = () => {
  authorization().then(response => {
    if (response[0]) {
      axios
        .post(
          '/api/comment/',
          {
            content: message.value,
            article_id: props.article.id,
            parent_id: parentID.value,
          },
          {
            headers: {
              Authorization: 'Bearer ' + localStorage.getItem('access.myblog'),
            },
          }
        )
        .then(response => {
          comments.value.unshift(response.data);
          message.value = '';
          alert('Comment submitted successfully!');
        });
    } else {
      alert('Please login before commenting!');
    }
  });
};
// ÂØπÊüêÊù°ËØÑËÆ∫ËøõË°åËØÑËÆ∫Ôºå Âç≥‰∫åÁ∫ßËØÑËÆ∫
const replyTo = comment => {
  parentID.value = comment.id;
  placeholder.value = 'Say to ' + comment.author.username;
};
// ‰øÆÊîπÊó•ÊúüÊòæÁ§∫Ê†ºÂºè
const formatted_time = formattedTime;
</script>

