  max-width: 100%; /* ËÆæÁΩÆÊúÄÂ§ßÂÆΩÂ∫¶‰∏∫100% */
  height: 6rem; /* ËÆæÁΩÆÂàùÂßãÈ´òÂ∫¶ */
  resize: vertical; /* ËÆæÁΩÆÂûÇÁõ¥Ë∞ÉÊï¥Â§ßÂ∞è */
}
