<template>
  <BlogHeader />

  <div id="article-creat">
    <h3>ÂèëË°®ÊñáÁ´† (ÊñáÁ´†„ÇíÁô∫Ë°®„Åô„Çã)</h3>
    <form id="image_form">
      <div class="form-elem">
        <span>Âõæ Áâá (ÂÜô Áúü): </span>
        <input
          @change="onFileChange"
          type="file"
          id="file"
          accept="image/gif, image/jpeg"
        />
      </div>
    </form>
    <form>
      <div class="form-elem">
        <span>Ê†á È¢ò („Çø„Ç§„Éà„É´):</span>
        <input v-model="title" type="text" placeholder="„Çø„Ç§„Éà„É´„ÇíÂÖ•Âäõ„Åô„ÇãÔºö" />
      </div>

      <div class="form-elem">
        <span>ÂàÜ Á±ª (ÂàÜ È°û):</span>
        <span v-for="category in categories" :key="category.id">
          <button
            class="category-btn"
            :style="categoryStyle(category)"
            @click.prevent="chooseCategory(category)"
          >
            {{ category.title }}
          </button>
        </span>
      </div>
    </form>
  </div>

  <div class="form-elem">
    <span>Ê≠£ Êñá: </span>
    <textarea
      v-model="body"
      placeholder="ËæìÂÖ•Ê≠£Êñá"
      rows="20"
      cols="80"
    ></textarea>
  </div>

  <div class="form-elem">
    <button @click.prevent="submit">Êèê‰∫§ (ÊèêÂá∫„Åô„Çã)</button>
  </div>

  <BlogFooter />
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import BlogHeader from '@/components/BlogHeader.vue';
import BlogFooter from '@/components/BlogFooter.vue';
import axios from 'axios';
import authorization from '@/utils/authorization';

const title = ref('');
const body = ref('');

// ÊâÄÊúâÂàÜÁ±ª
const categories = ref([]);
// ÈÄâÂÆöÁöÑÂàÜÁ±ª
const selectedCategory = ref(null);
// Ê†áÁ≠æ
const tags = ref('');

// Ê†áÈ¢òÂõæ
const avatarID = ref(null);

const router = useRouter();

onMounted(() => {
  // È°µÈù¢ÂàùÂßãÂåñÊó∂Ëé∑ÂèñÊâÄÊúâÂàÜÁ±ª
  axios
    .get('/api/category/')
    .then(response => (categories.value = response.data));
});

const onFileChange = e => {
  const file = e.target.files[0];
  // this.imageUrl = URL.createObjectURL(file);

  let formData = new FormData();
  formData.append('content', file);

  // ÁúÅÂéªÈâ¥ÊùÉÂíåÈîôËØØÂ§ÑÁêÜÁöÑÈÉ®ÂàÜ
  axios
    .post('/api/avatar/', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        Authorization: 'Bearer ' + localStorage.getItem('access.myblog'),
      },
    })
    .then(response => (avatarID.value = response.data.id));
};

// Ê†πÊçÆÂàÜÁ±ªÊòØÂê¶Ë¢´ÈÄâ‰∏≠ÔºåÊåâÈíÆÁöÑÈ¢úËâ≤ÂèëÁîüÂèòÂåñ
const categoryStyle = category => {
  if (
    selectedCategory.value !== null &&
    category.id === selectedCategory.value.id
  ) {
    return {
      backgroundColor: 'black',
    };
  }
  return {
    backgroundColor: 'lightgrey',
    color: 'black',
  };
};
// ÈÄâÂèñÂàÜÁ±ª
const chooseCategory = category => {
  // Â¶ÇÊûúÁÇπÂáªÂ∑≤ÈÄâÂèñÁöÑÂàÜÁ±ªÔºåÂàôÂ∞Ü selectedCategory ÁΩÆÁ©∫
  if (
    selectedCategory.value !== null &&
    selectedCategory.value.id === category.id
  ) {
    selectedCategory.value = null;
  } else {
    selectedCategory.value = category;
  }
};
// ÁÇπÂáªÊèê‰∫§ÊåâÈíÆ
const submit = () => {
  authorization().then(function (response) {
    if (response[0]) {
      let data = {
        title: title.value,
        body: body.value,
      };

      // Ê∑ªÂä†Ê†áÈ¢òÂõæ
      data.avatar_id = avatarID.value;

      // Ê∑ªÂä†ÂàÜÁ±ª
      if (selectedCategory.value) {
        data.category_id = selectedCategory.value.id;
      }
      // È¢ÑÂ§ÑÁêÜÂπ∂Ê∑ªÂä†Ê†áÁ≠æ
      // ÈÄóÂè∑ÂàÜÈöîÊ†áÁ≠æÂπ∂ÂâîÈô§Êó†ÊïàÊ†áÁ≠æ
      data.tags = tags.value
        .split(/[,Ôºå]/)
        .map(x => x.trim())
        .filter(x => x.charAt(0) !== '');

      // ÂèëÈÄÅÂèëË°®ÊñáÁ´†ËØ∑Ê±Ç
      // ÊàêÂäüÂêéÂâçÂæÄËØ¶ÊÉÖÈ°µÈù¢
      const token = localStorage.getItem('access.myblog');
      axios
        .post('/api/article/', data, {
          headers: { Authorization: 'Bearer ' + token },
        })
        .then(function (response) {
          router.push({
            name: 'ArticleDetail',
            params: { id: response.data.id },
          });
        });
    } else {
      alert('‰ª§ÁâåËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï„ÄÇ');
    }
  });
};
</script>

